@font-face {
    font-family: 'Myriad Pro Regular';
    src: url('../fonts/MyriadPro-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Roboto Regular';
    src: url('../fonts/Roboto-Regular.ttf');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Roboto Bold';
    src: url('../fonts/Roboto-Bold.ttf');
    font-weight: normal;
    font-style: normal;
}

@font-face {
	font-family: 'Charcoal CY';
    src: url('../fonts/charcoal-cy.ttf');
    font-weight: normal;
	font-style: normal;
}

$MPRegular: 'Myriad Pro Regular';
$RobotoR: 'Roboto Regular';
$RobotoB: 'Roboto Bold';


* {
    padding: 0;
    margin: 0;
    list-style: none;
    text-decoration: none;
}

.container {
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;
}

.button__wrapper {
    display: none;
    width: 100%;
    text-align: end;
}
.header {
    width: 100%;
    background-color:#297fb8;
    position: fixed;
    z-index: 2;
}

.header__button {
    width: 45px;
    height: 45px;
    border: 1px solid white;
    border-radius: 5px;
    background: none;
    margin: 10px;
    color: white;
    font-size: 25px;
    outline: none;
}

.header__list {
    display: flex;
    color: white;
    justify-content: center;
    vertical-align: middle;
    padding: 0 15px;
}

.list__item {
    font-family: $RobotoR;
    font-size: 14px;
    padding: 30px 15px;
    font-weight: 300;
    text-transform: uppercase;
    transition: 0.5s;
        a {
            color: white;
        }
    &:hover{ 
        background-color: darken($color: #297fb8, $amount: 10)
    }
}

.white__block {
    height: 90px;
    width: 100%;
    background-color: white;
}
.header__main {
    background: url(../image/Main__about.webp) center no-repeat;
    background-size: cover;
}

.main__about {
    display: flex;
    justify-content: space-between;
    padding: 0 15px;
    height: 510px;
    padding-bottom: 55px;
}

.main__info {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.info__description-first {
    color: #105eaa;
    font-family: 'Charcoal CY';
    font-size: 40px;
    font-weight: 400;
    line-height: 24px;
    text-transform: uppercase;
    margin-top: -15px
}

.info__photo-first {
    p {
        margin-left: -30px;
    }
}

.info__description-second p {
    &:first-line {
        color: #105eaa;
    }
    width: 185px;
    height: 51px;
    padding-left: 70px;
    margin-top: -20px;
    text-shadow: -1px 1px 1px #ffffff;
    font-family: "Charcoal CY";
    font-size: 28px;
    font-weight: 400;
    line-height: 30px;
    text-transform: uppercase;
    text-align: center;
    color: #ff8116;
}

.main__info_contacts {
    padding-top: 60px; 
}

.contacts {
    text-align: right;
}

.contacts__address {
    color: #2d354a;
    font-family: $RobotoR;
    font-size: 16px;
    font-weight: 400;
    line-height: 30px;
}
.contacts__phone {
    font-family: $RobotoB;
    font-size: 33px;
    font-weight: 700;
    line-height: 48px;
}

.call {
    margin-top: 40px;
}
.call-link {
    padding: 25px 15px;
    color: white;
    font-size: 16px;
    font-family: $MPRegular;
    background-color: #ff8a27;
    box-shadow: 5px 7px 0 #ffffff;
    transition: 0.5s;
        &:hover {
            background-color: darken($color: #ff8a27, $amount: 10)
        }
}

.windows {
    background: url(../image/Windows.webp) top center no-repeat;
    padding: 75px 0 95px;
    text-align: center;
}

.windows__title {
    color: #ffffff;
    font-family: $RobotoB;
    font-size: 48px;
    font-weight: 700;
}

.underscores {
    position: relative;
    margin: 30px auto;
    max-width: 550px;
    height: 1px;
    background-color: #ffffff;
        &::before {
            content: "";
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            width: 80px;
            height: 5px;
            background-color: #ff8a28;
            max-width: 100%;
            bottom: -2px;
        }
}

.windows__about {
    color: #ffffff;
    font-family: $RobotoR;
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
    padding-bottom: 60px;
    text-align: justify;
}

.h2__title { 
    color: #ffffff;
    font-family: $RobotoB;
    font-size: 42px;
    font-weight: 700;
}

.windows__buy {
    padding-bottom: 45px;
}

.windows__nav {
    padding-bottom: 95px;
}

.windows__list {
    display: flex;
    justify-content: space-between;
}

.windows__item {
    display: flex;
    flex-direction: column;
    figcaption  {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 216px;
        height: 90px;
        word-wrap: break-word;
        color: #2d354a;
        font-family: $RobotoB;
        font-size: 22px;
        font-weight: 700;
        line-height: 24px;
        border: 5px solid #ececec;
        border-top: none;
        margin-top: 10px;
        small {
            font-size: 16px;
            line-height: 30px;
        }
        &:hover {
            color: #3486bc;
            border-color: #297fb8;
        }
    }
}

.h2__title-black {
    text-align: center;
    color: #2d354a;
    font-family: $RobotoB;
    font-size: 42px;
    font-weight: 700;
    line-height: 30px;
}

.underscores-black {
    background-color: #2d354a;
}

.windows__instruction {
    color: #2d354a;
    font-family: $RobotoB;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: start;
    text-align: justify;
    p:not(:last-child){
        padding-bottom: 30px;
    }
}

.product {
    background-color: #2c3e50;
    padding: 80px 0 90px;
}

.product__info {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.window__title {
    color: #ffffff;
    font-family: $RobotoB;
    font-size: 30px;
    font-weight: 700;
    line-height: 30px;
    text-align: center;
    padding-bottom: 20px;
    position: relative;
    margin-bottom: 45px;
        &:before{
            content: '';
            position: absolute; 
            width: 80px;
            height: 5px;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            background-color: rgb(255, 138, 40);
        }
}

.window__photo {
    text-align: center;
    max-width: 350px;
    height: 315px;
    border-radius: 30px 30px 0 0;
    background-color: #ffffff;
    padding: 45px 0 60px;
    box-sizing: border-box;
}

.window__fullprice {
    padding: 25px 20px;
    background-color: #297fb8;
    color: white;
    margin-bottom: 15px;
}

.fullprice__price {
    position: relative;
    text-align: center;
    font-family: $RobotoR;
    font-size: 18px;
    font-weight: 400;
    padding-bottom: 16px;
    margin-bottom: 20px;
        span {
            font-family: $RobotoB;
            font-size: 36px;
            font-weight: 700;
        }

        &:before{
            content: '';
            position: absolute; 
            width: 80px;
            height: 5px;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            background-color: #00a0e3;
        }
}

.fullprice__list {
    font-family: $RobotoR;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    li { 
        font-size: 14px;
        list-style: disc inside;
    }
}

.window__call {
    width: 100%;
    box-shadow: 6px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 0 0 30px 30px;
    background-color: #ff8a28;
    font-family: $RobotoR;
    font-size: 24px;
    font-weight: 400;
    line-height: 30px;
    box-sizing: border-box;
     a{
        display: block;
        padding: 30px 55px;
        color: #ffffff;
     }

    &:hover {
        background-color: darken($color: #ff8a28, $amount: 10)
    }
}


.reasons {
    background: url(../image/reason_bg.webp) no-repeat center;
    background-size: cover;
    padding: 95px 0 70px;
    text-align: center;
}

.reasons__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.reasons__item {
    width: 216px;
    color: #ffffff;
    font-family: $RobotoB;
    font-size: 18px;
    font-weight: 500;
    line-height: 24px;
    margin: 0 10px;
    margin-bottom: 25px;
}
.reason_6 {
    width: 223px;
}

.conditions {
    padding: 100px 0 50px;
}

.conditions__container_options {
    margin-top: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.option {
    position: relative;
    max-width: 575px;
    width: 100%;
    height: 138px;
    margin-bottom: 20px;
        P{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        padding: 15px 0;
        background-color: rgba(41, 127, 184, 0.8);
        text-align: center;
        color: #fff;
        font-family: $RobotoB;
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        transition: 0.3s;
            &:hover {
                background-color: darken($color: #297FB8, $amount: 10)
            }
    }
}

.option_one {
    background: url(../image/Cond_1.webp)
}

.option_two {
    background: url(../image/Cond_2.webp)
}

.option_three {
    background: url(../image/Cond_3.webp)
}

.option_four {
    background: url(../image/Cond_4.webp)
}

.delivery {
    padding: 75px 0 200px;
    background: url(../image/bottom_bg.webp) center no-repeat;
    background-size: cover;
}

.conditions__title {
    margin-bottom: 10px;
}

.delivery__info {
    padding-top: 40px;
    color: #2d354a;
    font-family: $RobotoB;
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;

    & p:first-child {
        padding-bottom: 45px;
    }
}

.footer {
    background-color: #32ccce;
    padding-top: 6px;
}

.footer__context {
    background-color: #2d3e50;
    padding: 30px 0 50px;
}

.footer__info {
    display: flex;
    justify-content: space-between;
    color: white;
    text-align: center;
}

.footer__company {
    width: 210px;
}

.footer__company-title {
    font-size: 22px;
    font-family: $RobotoR;
    font-weight: 700;
}
.footer__logo {
    max-width: 100%;
}

.footer__company-owner {
    margin-top: 20px;
    font-size: 12px;
    font-family: $RobotoR;
}

.footer__contacts, .footer__working-time, .footer__creater {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.footer__contacts-phone  {
    font-size: 22px;
    font-family: $RobotoR;
}

.footer__contacts-adress {
    font-size: 16px;
    font-family: $RobotoR;
    padding: 20px 0;
}

.footer__contacts-email{
    font-size: 16px;
    font-family: $RobotoR;
     a {
         color: white;
     }
}

.footer__working-time-info {
    font-size: 16px;
    font-family: $RobotoR;
    &:nth-child(2) {
        padding: 20px 0;
    }
}

.footer__creater-logo{
    display: block;
    background-color: white;
    padding: 15px 40px;
    margin: 15px 0;
    color: black;
        img {
            margin-right: 5px;
        }
}

@media(max-width: 1315px) {
        
    .windows__list {
        flex-wrap: wrap;
    }
}

@media(max-width: 1200px) {
    .white__block {
        height: 80px;
    }
    .list__item {
        padding: 25px 9px;
    }

    .windows {
        background-position: top center;
    }

    .windows__list {
        justify-content: center;
    }
    .windows__item {
        margin: 0 15px 15px;
    }

    .product__info {
        justify-content: center;
    }
    .product__window {
        margin-right: 25px;
        &:not(:last-child) {

            margin-bottom: 35px;
        }
    }

    .conditions__container_options {
        justify-content: center;
    }
}

@media(max-width: 1080px) {
    .list__item {
        padding: 25px 6px;
        font-size: 11px;
    }
}

@media(max-width: 980px) {
    .footer__context {
        padding-bottom: 30px;
    }
    .footer__info {
        flex-direction: column;
        justify-content: center;
        align-items: center;
            .footer__company, .footer__contacts, .footer__working-time {
                width: 290px;
                padding-bottom: 10px;
                margin-bottom: 10px;
                border-bottom: 1px solid white;
            }
    }
}

@media(max-width: 840px) {
    .delivery {
        padding: 30px 0 90px;
    }

    .header__nav.container {
        padding: 0;
    }
    .header__list {
        display: flex;
        flex-direction: column;
        text-align: center;
        padding: 0px;
    }
    .header__hider {
        display: none;
    }
    .list__item {
        padding: 10px 0;
        font-size: 13px;
        width: 100%;
    }
    .button__wrapper {
        display: block;
    }

    .windows__about {
        padding-bottom: 25px;
    }

    .windows__buy {
        font-size: 28px;
        padding-bottom: 30px;
    }

    .windows {
        padding: 30px 0 45px;
    }
}

@media(max-width: 640px) {
    .main__about {
        flex-direction: column;
        align-items: center;
        height: 400px;
    }
    .main__info {
        margin-top: 20px;
        height: 200px;
    
    }
    .main__info_contacts {
        color: black;
        padding: 0;
    }

    .contacts__address {
        color: black;
        font-size: 18px;
    }

    .info__photo-first {
        img {
            margin-left: 20px;
            max-width: 320px;
        }
    }
    .info__description-first {
        margin-left: 20px;
        font-size: 35px;
    }

    .info__photo-second {
        display: none;
    }

    .windows__price {
        line-height: 45px;
    }

    .product__window{
        margin-right: 0;
    }

    .delivery__title {
        line-height: 45px;
    }
}

@media(max-width: 450px) {
    .underscores  {
        margin: 25px auto;
    }
    .windows__about {
        line-height: 25px;
    }
    .windows__buy {
        font-size: 22px;
    }
    .windows__instruction {
        font-size: 15px;
    }

    .windows__instruction p:not(:last-child) {
        padding-bottom: 15px;
    }

    .reasons {
        padding: 60px 0 50px;
    }
    .window__call {
        a {
            padding: 30px 25px;
        }
    }

    .conditions__title {
        font-size: 35px;
    }
}